.new-user{
    position: absolute;
}

.log-in-container > h2{
    font-family: LilitaOne;
    text-align: left;
}
button.select-button {
    width: 30vh;
    background-color: #63d0ff;
    color: black;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

select.select-box {
    width: 50vh;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

button.select-button:hover {
    background-color: rgba(7, 112, 196, 0.9);
}

.log-in-container > form {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}