.page{
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 10% 90%;
}

Header{
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 2;

}

.main-container{
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 2;
}

body > #root > div {
  height: 100vh;
}
